<template lang="html">
  <div class="box pageBox">
    <div class="level is-mobile step">
      <div class="level-left">
        <div class="level-item is-vcentered">
          <span v-if="completedReceivingWallet" class="stepCompleted fadeIn animated">
                <img src="../../../images/stepCompleted.png" alt="">
              </span>

          <span v-else class="stepImage">
                <img src="../../../images/step4.png" alt="">
              </span>
          <p class="stepTitle subtitle has-text-weight-semibold has-text-grey-dark">
            {{this.stepTitle}}
          </p>
        </div>
      </div>
      <div class="level-right">

        <a v-if="completedReceivingWallet" class="button is-outlined is-success completedButton" @click.prevent.stop="toggleCollapse()">
              <span class="buttonName">Completed</span>
            </a>

        <a @click.prevent.stop="toggleCollapse()" class="button is-medium" style="color:#b5b5b5;padding-left:15px;padding-right:15px;border:none;">
              <span v-show="isCollapsed" class="is-medium" style="width:24px">
                <i class="fa fa-angle-right"></i>
              </span>
              <span v-show="!isCollapsed" class="is-medium" style="width:24px">
                <i class="fa fa-angle-down"></i>
              </span>
            </a>
      </div>
    </div>

    <div :class="{ collapsed: isCollapsed }" class="collapsableContent fadeIn animated">
      <p class="notification">
        <span>
              <i class="fas fa-info-circle"></i>
            </span> Submit the your ERC20-compatible ETH wallet that will receive your SPOKKZ tokens.
      </p>
      <div class="notification is-warning has-text-weight-semibold">
        <span>
              <i class="fas fa-exclamation-triangle"></i>
            </span>
        Keep in mind: Use ERC20 compatible wallet only. Do not enter an exchange address.
      </div>

      <div v-if="error" class="animated fadeIn notification is-danger has-text-weight-semibold">
        {{ error }}
      </div>

      <form @submit.prevent="triggerReceivingWallet">
        <div class="field fadeIn animated">
          <label class="label">Your Receiving ETH (ERC20) Wallet Address</label>
            <p class="control">
              <input class="input is-medium wallet" type="text" v-model.trim="erc20_address" :disabled="disableInput">
            </p>
        </div>
        <div v-if="!completedReceivingWallet" class="field">
          <div class="control">
            <button class="auth-button button is-medium is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}"> Submit Address </button>
          </div>
        </div>
      </form>
     </div>
   </div>
</template>

<script>
import WAValidator from 'wallet-address-validator'

import { mapGetters, mapActions } from 'vuex'

export default {
  data() {
    return {
      isLoading: false,
      isCollapsed: true,
      erc20_address: '',
      error: ''
    }
  },
  computed: {
    stepTitle() {
      let result = "Step 4: Your Receiving Wallet"

      if (this.completedReceivingWallet) {
        result = "Your Receiving Wallet"
      }

      return result
    },
    disableInput() {
      if (!this.completedPersonalDetails) {
        return true
      } else if (!this.completedApproximateContribution) {
        return true
      } else if (this.completedReceivingWallet) {
        return true
      } else {
        return false
      }
    },
    disableButton() {
      if (!this.completedPersonalDetails) {
        return true
      } else if (!this.completedApproximateContribution) {
        return true
      } else if (!this.erc20_address) {
        return true
      } else {
        return false
      }
    },
    ...mapGetters('user', {
      completedApproximateContribution: 'completedApproximateContribution',
      completedPersonalDetails: 'completedPersonalDetails',
      completedReceivingWallet: 'completedReceivingWallet'
    })
  },
  methods: {
    triggerReceivingWallet() {
      this.error = ''
      this.isLoading = true

      let valid = false
      let payload = []

      valid = WAValidator.validate(this.erc20_address, 'ETH')

      if (!valid) {
        this.error = `Your ERC20 wallet address seems invalid. Please double check and try again.`
        this.isLoading = false
        return
      }

      payload.push({
        Name: 'custom:erc20_address',
        Value: this.erc20_address
      })

      this.updateAttribute(payload).then(result => {
          console.log('receiving wallet success');
          this.loadAttributes().then(() => {
            console.log('sync fields in loadAttributes')
            this.isCollapsed = true;
            this.syncFields();
          })
        })
        .catch(err => {
          console.error('unsuccessfullyr receiving wallet contribution', err)
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
    },
    ...mapActions('cognito', {
      updateAttribute: 'updateAttribute'
    }),
    ...mapActions('user', {
      loadAttributes: 'loadAttributes'
    }),
    syncFields() {
      this.erc20_address = this.$store.state.user.userAttributes["custom:erc20_address"]
    },
  },
  created() {
    // console.log('sync fields in created')
    this.syncFields()
  },
}
</script>

<style lang="css">
</style>



