<template>
<div id="portal">
  <app-header> </app-header>
  <div class="container">
    <div class="columns is-vcentered">
      <div class="column">
        <div class="customPanel">
          <div class="page">


            <div v-if="isFaq" class="pageHeading has-text-centered">
              <h1 class="title has-text-primary"> Frequently Asked Questions </h1>

              <router-link to="/" class="button is-primary is-rounded is-outlined">
                <span>Back to Home</span>
              </router-link>


            </div>

            <div v-else class="pageHeading has-text-centered">
              <h1 class="title has-text-primary"> The Spokkz Token Whitelist is open </h1>
              <h3 class="is-size-6 has-text-grey">Follow the steps below in order to buy Spokkz Tokens during crowdsale.</h3>
              <p class="is-size-6 has-text-grey"> Crowdsale date will be announced soon.</p>
            </div>


            <div v-if="notificationUploadDocument" class="notification is-warning">

              <p>
                <span class="has-text-weight-bold">Important:</span> Please complete <span class="is-italic has-text-weight-semibold">STEP 2: Identity Document</span>.
              </p>
            </div>

            <router-view/>
          </div>
        </div>
      </div>
    </div>
  </div>
  <app-footer> </app-footer>
  <span id="telegramHead">
    <a href="https://t.me/SpokkzOfficial" target="_blank">
    <img src="../../images/telegramHead.png" alt=""></a>
  </span>
</div>
</template>

<script>
import { mapGetters } from 'vuex'

import Header from './Header.vue'
import Footer from './Footer.vue'

export default {
  name: 'IcoTemplate',
  data() {
    return {
      pageName: ''
    }
  },
  computed: {
    ...mapGetters('user', {
      completedPersonalDetails: 'completedPersonalDetails',
      completedIdentityVerification: 'completedIdentityVerification',
      completedApproximateContribution: 'completedApproximateContribution'
    }),
    notificationUploadDocument() {
      return (this.completedPersonalDetails && !this.completedIdentityVerification && this.completedApproximateContribution)
    },
    isFaq() {
      return (this.pageName == 'faq')
    }
  },
  mounted() {
    this.pageName = this.$router.currentRoute.name;
  },
  beforeUpdate() {
    this.pageName = this.$router.currentRoute.name;
  },
  components: {
    'app-header': Header,
    'app-footer': Footer
  }
}
</script>

<style lang="scss">
@import "../../css/ico_base.scss";
#telegramHead {
    text-align: center;
    width: 80px;
    position: fixed!important;
    right: 0.5rem;
    bottom: 1rem;
    z-index: 99;
}

@include mobile {
    #telegramHead {
        width: 35px;
    }
};
</style>



