import {
  EMAIL_OCTOPUS_KEY
} from '../../global/resources.js'

import axios from 'axios'

const state = {
  user: null,
  userAttributes: {},
  preSignUp: {
  }
}

const getters = {
  getUser: state => {
    return state.user
  },
  getUserAttributes: state => {
    return state.userAttributes
  },
  getInitials: state => {
    if (state.userAttributes.given_name && state.userAttributes.given_name) {
        return `${state.userAttributes.given_name.substring(0,1)}${state.userAttributes.family_name.substring(0,1)}`
    }

  },
  getName: (state) => {
    if (state.userAttributes.given_name && state.userAttributes.given_name) {
      return `${state.userAttributes.given_name} ${state.userAttributes.family_name}`
    }
  },
  isAuthenticated: state => {
    return state.user !== null
  },
  completedPersonalDetails: state => {

    return !!(state.userAttributes.given_name &&
      state.userAttributes.family_name &&
      state.userAttributes["custom:id_type"] &&
      state.userAttributes["custom:id_number"] &&
      state.userAttributes["custom:issue_date"] &&
      state.userAttributes["custom:nationality"] &&
      state.userAttributes["custom:country"])
  },
  completedIdentityVerification: state => {
    return !!(state.userAttributes["custom:passport"], state.userAttributes["picture"])
  },
  completedApproximateContribution: state => {
    return !!(state.userAttributes["custom:exp_contribution"])
  },
  completedReceivingWallet: state => {
    return !!(state.userAttributes["custom:erc20_address"])
  },
  completedPayingWallet: state => {
    return !!(state.userAttributes["custom:eth_address"] || state.userAttributes["custom:btc_address"] || state.userAttributes["custom:zil_address"])
  },
  hasBoughtBefore: state => {
    return !!(state.userAttributes["custom:tx1"])
  },
  approvedKyc: state => {
    if (state.userAttributes["custom:approval_status"] == "CLEARED" || state.userAttributes["custom:approval_status"] == "ACCEPTED" ) {
      return true
    } else {
      return false
    }
  }
}

const mutations = {
  setPreSignUp: (state, attributes) => {
    state.preSignUp = { ...attributes}
  },
  setUser: (state, token) => {
    state.user = token
  },
  setUserAttributes: (state, attributes) => {
    let attrs = {}
    for(let a of attributes) {
      attrs[a.Name] = a.Value
    }
    state.userAttributes =  { ...attrs }
  },
  resetState: (state) => {
    state.user = null
    state.userAttributes = {}
    state.preSignUp = {}
  },
}

const actions = {
  loadUser: ({commit, dispatch, state, rootState}) => {
    return new Promise((resolve, reject) => {
      if (!state.user) {
        // console.log('getting token...')
        dispatch('cognito/getIdToken', null, { root: true}).then(token => {
          // console.log('found token', token)
          commit('setUser', token)
          dispatch('loadAttributes').then(() => {
            resolve()
          })
          .catch(() => {
            reject()
          })
        }).catch(err => {
          console.error(err);
          reject()
        })
      } else {
        console.log('user is logged in')
        resolve()
      }
    })
  },
  loadAttributes: ({commit, dispatch, state, rootState}) => {
    // console.log('getting attributes...')
    return new Promise((resolve, reject) => {
      dispatch('cognito/getUserAttributes', null, { root: true} ).then(attributes => {
        commit('setUserAttributes', attributes)
        resolve()
      }).catch(err => {
        console.log('cannot get/set attributes...')
        console.error(err);
        reject()
      })
    })
  },
  subscribeMailingList({state}) {
    let payload = {
      api_key: EMAIL_OCTOPUS_KEY,
      email_address: state.preSignUp.email,
      fields: {
        FirstName: state.preSignUp.firstName,
        LastName: state.preSignUp.lastName
      },
      status: "SUBSCRIBED"
    }

    let emailOctopusUrl = "https://snq3m3gpvd.execute-api.ap-southeast-1.amazonaws.com/prod/whitelist-email"
    axios({
      method: 'post',
      url: emailOctopusUrl,
      data: payload
    })
  },
  end: ({ commit }) => {
    commit('resetState')
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



