<template>
<div class="pageContent">

  <div class="has-shadow box formCard">
    <div class="level is-mobile">
      <router-link class="has-text-grey-light level-item" :to="{ name: 'register' }"> Register </router-link>
      <a class="level-item active has-text-primary">
                Log in
              </a>
    </div>

    <div v-if="message" class="animated fadeIn notification is-success">
      {{ message }}
    </div>

    <div v-if="error" class="animated fadeIn notification is-warning">
      {{ error }}
    </div>

    <form @submit.prevent="triggerSignIn" class="animated fadeIn">

      <!--Email-->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="email" v-model.trim="email" placeholder="Your Email Address" required>
          </span>
        </p>
      </div>

      <!-- Password -->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="password" v-model.trim="pass" placeholder="Your Password" required>
        </p>
      </div>

      <div class="field">
        <div class="control">
          <button class="auth-button button is-medium is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}"> Sign in</button>
        </div>
      </div>
    </form>

  </div>
  <p class="has-text-centered">
    <router-link :to="{name: 'forgot_password'}">Lost your password?</router-link>
  </p>
</div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  props: ['username', 'messageCode'],
  data() {
    return {
      error: '',
      email: '',
      pass: '',
      isLoading: false,
      message: ''
    }
  },
  computed: {
    disableButton() {
      return (!(this.email && this.pass));
    }
  },
  methods: {
    triggerSignIn() {
      this.isLoading = true
      this.error = ""
      this.signIn({
          email: this.email,
          pass: this.pass
        })
        .then(() => {
          this.$router.push('/ico/account')
        })
        .catch(err => {
          console.error();
          this.error = err.message
          this.isLoading = false

          if (err.code === "UserNotConfirmedException") {
            this.resendConfirmationCode(this.email)
              .then(() => {
                this.$router.push({ path: '/ico/confirm', query: { email: this.email } })
              }).catch((err) => {
                this.error = err.message
              })
          }
        })
        .finally(() => {

        })
    },
    ...mapActions('cognito', {
      signIn: 'signIn',
      resendConfirmationCode: 'resendConfirmationCode'
    })
  },
  created() {
    this.email = this.username

    if (this.messageCode === "EMAIL_VERIFIED") {
      this.message = 'Your email is verified. Please login again to continue.'
    } else if (this.messageCode === "PASSWORD_CHANGED") {
      this.message = 'Your password has been changed. Please login to continue.'
    }
  },
  metaInfo: {
    title: 'Spokkz ICO - Login'
  }
}
</script>

<style lang="scss" scoped>
</style>



