<template lang="html">
  <div class="box pageBox">
    <div class="level is-mobile step">
      <div class="level-left">
        <div class="level-item is-vcentered">
          <span v-if="completedPayingWallet" class="stepCompleted fadeIn animated">
                <img src="../../../images/stepCompleted.png" alt="">
              </span>

          <span v-else class="stepImage">
                <img src="../../../images/step5.png" alt="">
              </span>
          <p class="stepTitle subtitle has-text-weight-semibold has-text-grey-dark">
            {{this.stepTitle}}</p>
        </div>
      </div>
      <div class="level-right">

        <a v-if="completedPayingWallet" class="button is-outlined is-success completedButton" @click.prevent.stop="toggleCollapse()">
              <span class="buttonName">Completed</span>
            </a>

        <a @click.prevent.stop="toggleCollapse()" class="button is-medium" style="color:#b5b5b5;padding-left:15px;padding-right:15px;border:none;">
              <span v-show="isCollapsed" class="is-medium" style="width:24px">
                <i class="fa fa-angle-right"></i>
              </span>
              <span v-show="!isCollapsed" class="is-medium" style="width:24px">
                <i class="fa fa-angle-down"></i>
              </span>
            </a>
      </div>
    </div>

    <div :class="{ collapsed: isCollapsed }" class="collapsableContent fadeIn animated">
      <p class="notification">
        <span>
              <i class="fas fa-info-circle"></i>
            </span> Submit the wallet addresses that you're going to send funds from. The addresses you enter below must be exactly the same as the addresses you will send your payment from.

      </p>

      <div class="notification is-warning has-text-weight-semibold">
        <span>
              <i class="fas fa-exclamation-triangle"></i>
            </span>
        Keep in mind: Do not enter or send from an exchange address.
      </div>

      <div v-if="error" class="animated fadeIn notification is-danger">
        {{ error }}
      </div>

      <form @submit.prevent="triggerPayingWallet">
          <div class="field">
            <label class="label">Your ETH Wallet Address</label>
             <p class="control">
              <input class="input is-medium wallet" type="text" v-model.trim="eth_address" :disabled="completedPayingWallet">
            </p>
             <p v-if="walletErrors.eth_address_error" class="help is-danger">This ETH Address is invalid.</p>
          </div>

          <div class="divider has-text-centered">
            <span class="has-text-weight-bold"> AND/OR </span>
          </div>

          <div class="field">
            <label class="label">Your BTC Wallet Address</label>
            <p class="control">
              <input class="input is-medium wallet" type="text" v-model.trim="btc_address" :disabled="completedPayingWallet">
            </p>
            <p v-if="walletErrors.btc_address_error" class="help is-danger">This BTC Address is invalid.</p>
          </div>

          <div class="divider has-text-centered">
            <span class="has-text-weight-bold"> AND/OR </span>
          </div>

          <div class="field">
            <label class="label">Your MainNet ONT Wallet Address (Not NEP-5 ONT)</label>
            <p class="control">
              <input class="input is-medium wallet" type="text" v-model.trim="zil_address" :disabled="completedPayingWallet">
            </p>
          </div>

          <div v-if="!completedPayingWallet" class="field">
            <div class="control">
              <button class="auth-button button is-medium is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}"> Submit Address </button>
            </div>
          </div>
      </form>
    </div>
  </div>
</template>

<script>
import WAValidator from 'wallet-address-validator'

import { mapGetters, mapActions } from 'vuex'

export default {
  data() {
    return {
      eth_address: '',
      btc_address: '',
      zil_address: '',
      isLoading: false,
      isCollapsed: true,
      error: '',
      walletErrors: {
        eth_address_error: false,
        btc_address_error: false,
        zil_address_error: false
      }
    }
  },
  computed: {
    stepTitle() {
      let result = "Step 5: Your Sending Wallet"

      if (this.completedReceivingWallet) {
        result = "Your Sending Wallet"
      }

      return result
    },
    disableButton() {
      if (!this.completedPersonalDetails) {
        return true
      } else if (!this.completedApproximateContribution) {
        return true
      } else if (!this.completedReceivingWallet) {
        return true
      } else if (this.eth_address || this.btc_address || this.zil_address) {
        return false
      } else {
        return true
      }
    },
    ...mapGetters('user', {
      completedApproximateContribution: 'completedApproximateContribution',
      completedPersonalDetails: 'completedPersonalDetails',
      completedReceivingWallet: 'completedReceivingWallet',
      completedPayingWallet: 'completedPayingWallet',
    })
  },
  methods: {
    resetWalletErrors() {
      this.walletErrors = {
        eth_address_error: false,
        btc_address_error: false,
        zil_address_error: false
      }
    },
    triggerPayingWallet() {
      let payload = []
      this.error = ''
      this.resetWalletErrors()
      this.isLoading = true

      let generalValidation = false
      generalValidation = !!(this.eth_address || this.btc_address || this.eth_address)

      if (generalValidation == false) {
        this.error = "Please add at least one wallet address."
        this.isLoading = false
        return
      }

      let valid;

      if (this.eth_address) {
        valid = WAValidator.validate(this.eth_address, 'ETH')
        if (valid) {
          payload.push({
            Name: 'custom:eth_address',
            Value: this.eth_address,
          })
        } else {
          this.error = "Some wallet addresses below are invalid. Please double check and try again."
          this.walletErrors.eth_address_error = true
          this.isLoading = false
          return
        }
      }

      if (this.btc_address) {
        valid = WAValidator.validate(this.btc_address, 'BTC')
        if (valid) {
          payload.push({
            Name: 'custom:btc_address',
            Value: this.btc_address,
          })
        } else {
          this.error = "Some wallet addresses below are invalid. Please double check and try again."
          this.walletErrors.btc_address_error = true
          this.isLoading = false
          return
        }
      }

      if (this.zil_address) {
        // valid = WAValidator.validate(this.zil_address, 'ETH')
        // if (valid) {
        payload.push({
          Name: 'custom:zil_address',
          Value: this.zil_address,
        })
        // } else {
        //   this.error = "Some wallet addresses below are invalid. Please double check and try again."
        //   this.walletErrors.zil_address_error = true
        //   this.isLoading = false
        //   return
        // }
      }


      console.log('payload for paying wallet', payload)
      this.updateAttribute(payload).then(result => {
          console.log('paying wallet success');
          this.loadAttributes().then(() => {
            console.log('sync fields in loadAttributes')
            this.isCollapsed = true;
            this.syncFields();
          })
        })
        .catch(err => {
          console.error('unsuccessfully paying wallet contribution', err)
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
    },
    ...mapActions('cognito', {
      updateAttribute: 'updateAttribute'
    }),
    ...mapActions('user', {
      loadAttributes: 'loadAttributes'
    }),
    syncFields() {
      this.eth_address = this.$store.state.user.userAttributes["custom:eth_address"]
      this.btc_address = this.$store.state.user.userAttributes["custom:btc_address"]
      this.zil_address = this.$store.state.user.userAttributes["custom:zil_address"]
    },
  },
  created() {
    // console.log('sync fields in created')
    this.syncFields()
  },
}
</script>

<style lang="css">
</style>



