<template>
<div class="pageContent">
  <div class="has-shadow box formCard">
    <div class="level is-mobile">
      <a class="level-item active has-text-primary"> Verify Email
              </a>
    </div>

    <div class="notification">
      We have sent a temporary verification code to
      <span class="has-text-weight-bold"> {{ username }} </span>. Enter the code to verify your email.
    </div>

    <div v-if="error" class="animated fadeIn notification is-warning">
      {{ error }}
    </div>

    <form @submit.prevent="triggerConfirmRegistration">
      <!--Code-->
      <div class="field">
        <label class="label">Verfication Code</label>
        <p class="control">
          <input class="input" type="text" v-model.trim="code" required>
        </p>
      </div>

      <div class="field">
        <div class="control">
          <button class="auth-button button is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}">Verify</button>
        </div>
      </div>
    </form>
  </div>

  <p class="has-text-centered">
    If you cannot find your cofirmation email, please check your spam folder, or
    <a href="#" @click.prevent="triggerResendConfirmation" class="is-secondary"> click here to resend the confirmation email.</a>
  </p>
</div>
</template>
<script>
import { mapActions } from 'vuex';
export default {
  props: ['email'],
  data() {
    return {
      error: '',
      code: '',
      username: '',
      isLoading: false
    }
  },
  computed: {
    disableButton() {
      return (!(this.username && this.code));
    }
  },
  methods: {
    triggerConfirmRegistration() {
      this.isLoading = true
      this.error = ""

      if (!(this.username && this.code)) {
        return alert('Must have both username and code filled out!')
      }
      this.confirmRegistration({ username: this.username, code: this.code })
        .then(() => {
          this.subscribeMailingList()
            .catch(err => {
              console.error();
            }).finally(() => {
              this.$store.commit('user/setPreSignUp', {})
              this.$router.push({ path: '/ico/login', query: { username: this.username, messageCode: 'EMAIL_VERIFIED' } })
            })
        })
        .catch((err) => {
          console.error();
          this.error = err.message
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    triggerResendConfirmation() {
      console.log('resending confirmation');
      this.resendConfirmationCode(this.email).then(() => {}, () => {
        alert('there was an error')
      })
    },
    ...mapActions('cognito', {
      confirmRegistration: 'confirmRegistration',
      resendConfirmationCode: 'resendConfirmationCode'
    }),
    ...mapActions('user', {
      subscribeMailingList: 'subscribeMailingList'
    })
  },
  created() {
    this.username = this.email
  }
}
</script>

<style lang="scss" scoped>
</style>



