<template>
<div class="pageContent">
  <div class="has-shadow box formCard">
    <div class="level is-mobile">
      <a class="level-item active has-text-primary"> Forgot Password </a>
    </div>
    <div v-if="response" class="animated fadeIn notification is-warning">
      {{ response }}
    </div>

    <form v-if="step === 1" @submit.prevent="triggerResetPassword()" class="animated fadeIn">
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="email" v-model.trim="email" placeholder="Your Email Address" required>
        </p>
      </div>

      <div class="field">
        <div class="control">
          <button class="auth-button button is-medium is-primary" :disabled="disableResetMyPassButton" :class="{'is-loading': isLoading}"> Reset my Password</button>
        </div>
      </div>
    </form>

    <form v-if="step === 2" @submit.prevent="triggerConfirmPassword()" class="animated fadeIn">
      <div class="notification">
        We have sent a verification code to
        <span class="has-text-weight-bold"> {{ email }} </span>. Enter the verification code and your new password below.
      </div>

      <div class="field">
        <p class="control">
          <input class="input is-medium" type="text" v-model.trim="code" required placeholder="Verification Code">
        </p>
      </div>

      <!-- Password -->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="password" v-model.trim="newPass" placeholder="Your New Password" required>
        </p>
        <p class="help"> Requires minimum of 8 characters</p>
      </div>

      <div class="field">
        <div class="control">
          <button class="auth-button button is-medium is-primary" :disabled="disableUpdateMyPassButton" :class="{'is-loading': isLoading}"> Update Password </button>
        </div>
      </div>
    </form>
  </div>
  <p class="has-text-centered">
    Don't have an account?
    <router-link :to="{ name: 'register' }">Register</router-link>
  </p>
</div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  data() {
    return {
      email: '',
      response: '',
      step: 1,
      code: '',
      newPass: '',
      isLoading: false
    }
  },
  computed: {
    disableResetMyPassButton() {
      return (!this.email)
    },

    disableUpdateMyPassButton() {
      return !(this.email && (this.newPass.length >= 8))
    }
  },
  methods: {
    triggerResetPassword() {
      this.isLoading = true

      this.resetPassword(this.email)
        .then((data) => {
          this.step = 2
        })
        .catch((err) => {
          this.response = err.message
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    triggerConfirmPassword() {
      this.isLoading = true
      this.confirmPassword({ code: this.code, pass: this.newPass, username: this.email })
        .then(() => {
          this.$router.push({
            path: '/ico/login',
            query: {
              username: this.email,
              messageCode: "PASSWORD_CHANGED"
            }
          })
        })
        .catch((err) => {
          this.response = err.message
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    ...mapActions('cognito', {
      resetPassword: 'forgotPassword',
      confirmPassword: 'confirmPassword'
    })
  }
}
</script>

<style lang="scss" scoped>
@import "../../css/core_deep-blue.scss";
.clean-signup-card {
    max-width: 600px;
}

img {
    width: 200px;
    margin-bottom: 25px;
}
</style>



