<template>
<div class="pageContent">
  <div v-for="section in sections" class="faqSection">
    <p class="title is-size-6 has-text-centered"> {{ section.sectionName }}</p>
    <div class="box" v-for="faq in section.faqs">
      <div class="has-text-weight-semibold question is-size-5 is-size-6-mobile">
        <span class="icon">
          <i class="fas fa-question-circle"></i>
        </span>
        <p>{{ faq.question }}</p>
      </div>

      <div class="content" v-html="faq.answer">
      </div>

    </div>
    <div class="divider has-text-centered"> </div>
  </div>
</div>
</template>
<script>
import faqProceed from '../../images/faq/proceeds.png'
import faqAllocation from '../../images/faq/allocation.png'

export default {


  data() {
    return {
      sections: [{
        sectionName: "SPOKKZ PLATFORM",
        faqs: [{
            question: "What is SPOKKZ?",
            answer: "<p>Spokkz will use Blockchain Technology to disrupt the $2 Trillion USD traditional Media Industry by creating a decentralized ecosystem and marketplace where film lovers will join advertisers, stars, content creators and merchants as stakeholders.</p>",
          },
          {
            question: "Why use blockchain?",
            answer: "<p>It intends to use Blockchain technology to disrupt the entire value chain of the movie making and content creation by bringing <a class='has-text-link has-text-weight-bold' href='https://spuul.com'>Spuul</a>'s viewer base (60 million as at May 2018) and over 100 movie producers and content creators closer together to increase value and reduce costs.</p>",
          },
          {
            question: "Why offer an ICO?",
            answer: "<p>In order to accomplish the objectives of platform growth, penetration into key languages and geographies, Spuul intends to raise funds through an Initial Coin Offering ('ICO').</p>"
          }
        ]
      }, {
        sectionName: "SPOKKZ PLATFORM TOKEN - (SPKZ)",
        faqs: [{
            question: "What is the maximum total supply of SPKZ tokens?",
            answer: "<p>1 Billion.</p>",
          },
          {
            question: "How many SPOKKZ tokens will be sold during the ICO?",
            answer: "<p>300 million, 30% of the maximum total supply.</p>",
          },
          {
            question: "What is the hard cap?",
            answer: "<p>$12 million</p>",
          },
          {
            question: "What is the soft cap?",
            answer: "<p>$5 million</p>",
          },
          {
            question: "What will happen to unsold tokens?",
            answer: "<p>The unsold tokens get burned.</p>",
          },
          {
            question: "Will there be a Pre-sale?",
            answer: "<p>Yes, with different bonus stuctures per stage. Starts on June 28, 2018, ends on September 9, 2018.</p>",
          },
          {
            question: "When will the token sale end?",
            answer: "<p>TBD</p>",
          },
          {
            question: "Will there be a KYC and AML?",
            answer: "<p>Yes, this is a mandatory requirement by the Monetary Authority of Singapore before anyone can purchase our tokens.</p>",
          },
          {
            question: "What forms of payment are accepted in the ICO?",
            answer: "<p>ETH, BTC, ONT, Cash</p>",
          },

          {
            question: "What is the price of 1 SPKZ?",
            answer: "<p>1 SPKZ = 0.08 USD</p>",
          },
          {
            question: "What will be the token allocation?",
            answer: `<p class='has-text-centered'><img src=${faqAllocation}> </p>`,
          },
          {
            question: "What will the token launch proceeds be used for?",
            answer: `<p class='has-text-centered'><img src=${faqProceed}> </p>`,
          },
          {
            question: "Will there be a lockup period for SPKZ tokens sold in the token launch?",
            answer: "No lock-in period.",
          },
        ]
      }]
    }
  },
  metaInfo: {
    title: 'Spokkz FAQ'
  }
}
</script>

<style lang="scss" scoped>
@import "../../css/ico_base.scss";

.primaryColor {
    color: #F14900;
}
.question {

    display: flex;
    align-items: center;
    border: none;
    color: $primary;
    padding: 0 0 10px;

    .icon {
        color: #F14900;
        padding-right: 10px;
    }
}

.faqSection {
    margin-bottom: 30px;

    .title {
        color: #F14900;
    }
}
</style>



