import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome/Welcome'
import Maintenance from '@/components/Maintenance'
import IcoTemplate from '@/components/Ico/IcoTemplate'
import Faq from '@/components/Ico/Faq'
import Register from '@/components/Ico/Register'
import Confirm from '@/components/Ico/Confirm.vue'
import Login from '@/components/Ico/Login'
import ForgotPassword from '@/components/Ico/ForgotPassword'
import Account from '@/components/Ico/Account'
import Buy from '@/components/Ico/Buy'
import Orders from '@/components/Ico/Orders'
import PageNotFound from '@/components/PageNotFound'


import { store } from '@/store/store'

Vue.use(Router)

function signOut(to, from, next) {
  console.log('signing out');
  store.dispatch('user/end')
  store.dispatch('cognito/signOut').then(() => {
    next({
      path: '/ico/login'
    })
  })
}

export default new Router({
  routes: [
    {
      path: '/ico',
      component: IcoTemplate,
      children: [{
          path: 'register',
          name: 'register',
          component: Register
        },
        {
            path: 'faq',
            name: 'faq',
            component: Faq
        },
        {
          path: 'confirm',
          name: 'confirm',
          component: Confirm,
          props: (route) => ({
            email: route.query.email
          })
        },
        {
          path: 'login',
          name: 'login',
          component: Login,
          props: (route) => ({
            username: route.query.username,
            messageCode: route.query.messageCode
          })
        },
        {
          path: "sign_out",
          beforeEnter: signOut
        },
        {
          path: 'forgot_password',
          name: 'forgot_password',
          component: ForgotPassword
        },
        {
          path: 'account',
          name: 'account',
          component: Account,
          meta: {
            loadUser: true
          }
        }, {
          path: 'buy',
          name: 'buy',
          component: Buy,
          meta: {
            loadUser: true
          }
        },
        {
          path: 'orders',
          name: 'orders',
          component: Orders,
          meta: {
            loadUser: true
          }
        }

      ]
    },
    {
      path: '/',
      name: 'Welcome',
      component: Welcome,
      props: (route) => ({
        v: route.query.v
      })
    }
  ],
  mode: 'history'
})



