<script>
import FlipCountdown from 'vue2-flip-countdown'
import { Carousel, Slide } from 'vue-carousel'

import videoExplainer from '../../media/explainer.mp4'
import posterExplainer from '../../images/posters/explainer.png'

import videoInterview1 from '../../media/interview-1.mp4'
import videoInterview2 from '../../media/interview-2.mp4'
import videoInterview3 from '../../media/interview-3.mp4'
import videoInterview4 from '../../media/interview-4.mp4'
import videoInterview5 from '../../media/interview-5.mp4'
import videoInterview6 from '../../media/interview-6.mp4'

import posterInterview1 from '../../images/posters/interview-1.png'
import posterInterview2 from '../../images/posters/interview-2.png'
import posterInterview3 from '../../images/posters/interview-3.png'
import posterInterview4 from '../../images/posters/interview-4.png'
import posterInterview5 from '../../images/posters/interview-5.png'
import posterInterview6 from '../../images/posters/interview-6.png'


window.videojs = require('video.js')

export default {
  props: ['v'],
  data() {
    return {
      activeMenu: false,
      activePlayer: false,
      activePlayerVideoId: '',
      activeDropdownLanguage: false,
      activeHPlayer: false,
      player: '',
      url: videoExplainer,
      poster: posterExplainer,
      volume: 1,
      interviews: {
        interview1: {
          url: videoInterview1,
          poster: posterInterview1
        },
        interview2: {
          url: videoInterview2,
          poster: posterInterview2
        },
        interview3: {
          url: videoInterview3,
          poster: posterInterview3
        },
        interview4: {
          url: videoInterview4,
          poster: posterInterview4
        },
        interview5: {
          url: videoInterview5,
          poster: posterInterview5
        },
        interview6: {
          url: videoInterview6,
          poster: posterInterview6
        }
      }

    }
  },
  computed: {
    selectedLanguage: {
      get() {
        return this.$i18n.locale
      },
      set(newValue) {
        this.activeDropdownLanguage = false
        this.$i18n.locale = newValue
      }
    },
    bonusDescription() {
      let date = new Date()

      let bonusStructure = [
        [new Date(2018, 5, 28, 0, 0), new Date(2018, 6, 15, 23, 59)],
        [new Date(2018, 6, 16, 0, 0), new Date(2018, 6, 29, 23, 59)],
        [new Date(2018, 6, 30, 0, 0), new Date(2018, 7, 12, 23, 59)],
        [new Date(2018, 7, 13, 0, 0), new Date(2018, 7, 26, 23, 59)],
        [new Date(2018, 7, 27, 0, 0), new Date(2018, 8, 9, 23, 59)]
      ]

      switch (true) {
        case (date >= bonusStructure[0][0] && date <= bonusStructure[0][1]):
          return "BONUS UP TO 100%"
        case (date >= bonusStructure[1][0] && date <= bonusStructure[1][1]):
          return "BONUS UP TO 80%"
        case (date >= bonusStructure[2][0] && date <= bonusStructure[2][1]):
          return "BONUS UP TO 60%"
        case (date >= bonusStructure[3][0] && date <= bonusStructure[3][1]):
          return "BONUS UP TO 40%"
        case (date >= bonusStructure[4][0] && date <= bonusStructure[4][1]):
          return "BONUS UP TO 20%"
      }
    }
  },
  methods: {
    toggleMenu() {
      this.activeMenu = !this.activeMenu
    },
    toggleHPlayer(video) {
      this.activeHPlayer = !this.activeHPlayer

      if (!this.activeHPlayer) {
        this.player.pause();
        this.player.currentTime(0);
      } else {

        if (video) {
          this.playerSetSrc(video.url);
          this.playerSetPoster(video.poster);
        } else {
          this.playerSetSrc(this.url);
          this.playerSetPoster(this.poster);
        }

        this.player.play();
      }
    },
    toggleActivePlayer(video_id) {
      if (video_id) {
        this.activePlayerVideoId = video_id;
      }
      this.activePlayer = !this.activePlayer;
    },
    playerInitialize() {
      this.player = videojs('myPlayer');
    },
    playerDispose() {
      this.player.dispose();
    },

    playerPlay() {
      this.player.play();
    },
    playerPause() {
      this.player.pause();
    },


    playerSetSrc(url) {
      this.player.src(url);
    },
    playerSetVolume(float) {
      this.player.volume(float);
    },
    playerSetPoster(url) {
      this.player.poster(url);
    },
    playerSetTime(time) {
      this.player.currentTime(time);
    },


    playerEventEnded() {
      console.log('ended');
    },
    playerEventVolume() {
      this.volume = this.player.volume();
    },
    playerEventError() {
      console.log(this.playerGetError())
    },
    playerGetPaused() {
      return this.player.paused();
    },
    playerGetTime() {
      return this.player.currentTime();
    },
    playerGetError() {
      return this.player.error().message;
    },


    playerSetupEvents() {
      this.player.on('ended', function() { var a = window.playerEvents.playerEventEnded(); });
      this.player.on('volumechange', function() { window.playerEvents.playerEventVolume(); });
      this.player.on('error', function() { window.playerEvents.playerEventError(); });
    },
  },
  mounted() {
    window.playerEvents = this;
    this.playerInitialize();
    this.playerSetSrc(this.url);
    this.playerSetPoster(this.poster);
    this.playerSetupEvents();
  },
  beforeDestroy() {
    this.playerDispose();
  },
  metaInfo: {
    title: 'Spokkz: A Community-Powered Film & TV Blockchain Ecosystem',
    meta: [
      { property: 'og:site_name', content: 'SPOKKZ' },
      { property: 'og:type', content: 'website' },
      { property: 'og:url', content: 'https://spokkz.com' },
      { property: 'og:image', content: 'https://spokkz.com/static/spokkz_preview.png' },
      { property: 'og:description', content: 'ICO kicks off with 60 million user wallets. View, vote, fund and support your favourite media projects in a de-centralised ecosystem and marketplace.' },

      // Twitter card
      { name: 'twitter:card', content: 'summary_large_image' },
      { name: 'twitter:site', content: 'https://spokkz.com' },
      { name: 'twitter:title', content: 'Spokkz: A Community-Powered Film & TV Blockchain Ecosystem' },
      { name: 'twitter:description', content: 'ICO kicks off with 60 million user wallets. View, vote, fund and support your favourite media projects in a de-centralised ecosystem and marketplace.' },
      // Your twitter handle, if you have one.
      { name: 'twitter:creator', content: '@SpokkzOfficial' },
      { name: 'twitter:image:src', content: 'https://spokkz.com/static/spokkz_preview.png' },

      // Google / Schema.org markup:
      { itemprop: 'name', content: 'Spokkz: A Community-Powered Film & TV Blockchain Ecosystem' },
      { itemprop: 'description', content: 'ICO kicks off with 60 million user wallets. View, vote, fund and support your favourite media projects in a de-centralised ecosystem and marketplace.' },
      { itemprop: 'image', content: 'https://spokkz.com/static/spokkz_preview.png' }
    ]
  },
  components: {
    Carousel,
    Slide,
    FlipCountdown
  },
  created() {
    if (this.v) {
      this.activePlayerVideoId = this.v
      this.activePlayer = true
    }
  }
}
</script>

