// import {CognitoUserPool, CognitoUser, AuthenticationDetails, CognitoUserAttribute} from 'amazon-cognito-identity-js'
import {
  CognitoUserPool,
  CognitoUser,
  AuthenticationDetails,
  CognitoUserAttribute
} from 'amazon-cognito-identity-js'

import AWS from 'aws-sdk'
import axios from 'axios'

import {
  USER_POOL_ID,
  USER_POOL_CLIENT_ID,
  AWS_REGION,
  IDENTITY_POOL_ID,
  BUCKET_USER_UPLOAD
} from '../../global/resources.js'


const config = {
  userPoolId: USER_POOL_ID,
  clientId: USER_POOL_CLIENT_ID,
  region: AWS_REGION
}

function handleResults(err, result, success, failure) {
  if (err) {
    console.error(err);
    return failure(err);
  } else {
    return success(result);
  }
}

const state = {
  userPool: new CognitoUserPool({
    UserPoolId: config.userPoolId,
    ClientId: config.clientId
  })
}

const getters = {}
const mutations = {
}

const actions = {
  signUp({commit, state}, user) {
    return new Promise((resolve, reject) => {
      let attributeList = []

      if (user.firstName) {
        attributeList.push(new CognitoUserAttribute({ Name: 'given_name', Value: user.firstName }))
      }

      if (user.lastName) {
        attributeList.push(new CognitoUserAttribute({ Name: 'family_name', Value: user.lastName }))
      }

      state.userPool.signUp(user.email, user.pass, attributeList, null, (err, result) => {
        return handleResults(err, result, resolve, reject);
      })
    })
  },
  signIn({commit, state}, user) {
    let authenticationDetails = new AuthenticationDetails({
      Username: user.email,
      Password: user.pass
    })

    let cognitoUser = new CognitoUser({
      Username: user.email,
      Pool: state.userPool
    })

    return new Promise((resolve, reject) => {
      cognitoUser.authenticateUser(authenticationDetails, {
        onSuccess: session => {
          resolve()
        },
        onFailure: err => {
          console.error(err);
          reject(err)
        }
      })
    })
  },
  signOut({commit, state}) {
    let user = state.userPool.getCurrentUser();
    if (!user) {
      return;
    }
    if(AWS.config.credentials) {
      AWS.config.credentials.clearCachedId()
    }

    return user.signOut();
  },
  getIdToken({commit, state}) {
    return new Promise((resolve, reject) => {
      let user = state.userPool.getCurrentUser()
      if (user) {
        user.getSession((err, session) => {
          if (err) return reject(err);
          let token = session.getIdToken().getJwtToken();
          resolve(token);
        })
      } else {
        reject(new Error("user is not logged in"))
      }
    })
  },
  getUserAttributes({commit, state}) {
    return new Promise((resolve, reject) => {
      let user = state.userPool.getCurrentUser();
      if(user) {
        user.getSession((err, session) => {
          user.getUserAttributes(function(err, attributes) {
            if (err) return reject(err);
            resolve(attributes);
          })
        })
      } else {
        reject(new Error("user is not logged in"))
      }
    })
  },
  updateAttribute({commit, state}, attributArray) {
    return new Promise((resolve, reject) => {
      let user = state.userPool.getCurrentUser()
      if(user) {
        user.getSession((err, session) => {
          if (err) return reject(err);

          let attributeList = []

          for(let x of attributArray) {
            attributeList.push(new CognitoUserAttribute(x))
          }


          user.updateAttributes(attributeList, (err, result) => {
            return handleResults(err, result, resolve, reject);
          })
        })

      } else {
        console.error('user is not logged in')
        reject(new Error("user is not logged in"))
      }
    })
  },
  updateAttributes({commit, state}, payload) {
    return new Promise((resolve, reject) => {
      console.log(state)

      let artemisUrl = "https://6b6lac6bo4.execute-api.ap-southeast-1.amazonaws.com/prod/individual"
      let artemisPayload = {
        "domain_name": "SPUUL",
	      "rfrID": payload.email,
	      "first_name": payload.given_name,
	      "last_name": payload.family_name,

        // "identification_type": payload.id_type,
        // "identification_number": payload.id_number,
        // "issue_date": payload.issue_date,
      	// "expiry_date": payload.expiry_date,

        "nationality": payload.nationality,
      	"country_of_residence": payload.country,
      	"onboarding_mode": "NON FACE-TO-FACE",
      	"product_service_complexity": "COMPLEX",
      	"payment_mode": "VIRTUAL CURRENCY",
      	"ssic_code": "UNKNOWN - UNKNOWN",
      	"ssoc_code": "UNKNOWN - UNKNOWN"
      }

      let headers = {
          "Content-Type": "application/json",
            "WEB2PY-USER-TOKEN": "799d2da6-4522-4814-8c72-66111209556c",
      }

      // Send a POST request
      axios({
        method: 'post',
        url: artemisUrl,
        data: artemisPayload,
        headers: headers
      }).then((res) => {
        let user = state.userPool.getCurrentUser()

        if(user) {
          user.getSession((err, session) => {
            if (err) return reject(err);

            let  attributeList = []
            let givenNameAttribute = { Name: 'given_name',Value: payload.given_name }
            let familyNameAttribute = { Name: 'family_name', Value: payload.family_name}

            let idTypeAttribute = { Name: 'custom:id_type', Value: payload.id_type}
            let idNumberAttribute = { Name: 'custom:id_number', Value: payload.id_number}
            let issueDateAttribute = { Name: 'custom:issue_date', Value: payload.issue_date}
            let expirtyDateAttribute = { Name: 'custom:expiry_date', Value: payload.expiry_date}

            let nationalityAttribute = { Name: 'custom:nationality', Value: payload.nationality }
            let countryAttribute = { Name: 'custom:country', Value: payload.country }
            let telegramAttribute = { Name: 'custom:telegram',Value: payload.telegram}

            attributeList.push(new CognitoUserAttribute(givenNameAttribute))
            attributeList.push(new CognitoUserAttribute(familyNameAttribute))

            attributeList.push(new CognitoUserAttribute(idTypeAttribute))
            attributeList.push(new CognitoUserAttribute(idNumberAttribute))
            attributeList.push(new CognitoUserAttribute(issueDateAttribute))
            attributeList.push(new CognitoUserAttribute(expirtyDateAttribute))

            attributeList.push(new CognitoUserAttribute(nationalityAttribute))
            attributeList.push(new CognitoUserAttribute(countryAttribute))
            attributeList.push(new CognitoUserAttribute(telegramAttribute))



            if( res.data.status ) {
              let kyc_status = { Name: 'custom:kyc_status',Value: res.data.status}
              attributeList.push(new CognitoUserAttribute(kyc_status))
            }

            if( res.data.approval_status) {
                let approval_status = { Name: 'custom:approval_status',Value: res.data.approval_status}
                attributeList.push(new CognitoUserAttribute(approval_status))
            }

            user.updateAttributes(attributeList, (err, result) => {
              return handleResults(err, result, resolve, reject);
            });
          });
        } else {
          console.error('user is not logged in');
          reject(new Error("user is not logged in"))
        }
      }).catch((err) => {
        console.log(err)
      })
    })
  },
  confirmRegistration({commit, state}, {username, code}) {

    let cognitoUser = new CognitoUser({
      Username: username,
      Pool: state.userPool
    });
    return new Promise((resolve, reject) => {
      cognitoUser.confirmRegistration(code, true, (err, result) => {
        return handleResults(err, result, resolve, reject);
      });
    });
  },
  resendConfirmationCode({commit, state}, username) {
    let cognitoUser = new CognitoUser({
      Username: username,
      Pool: state.userPool
    });
    return new Promise((resolve, reject) => {
      cognitoUser.resendConfirmationCode((err, result) => {
        return handleResults(err, result, resolve, reject);
      });
    });
  },
  forgotPassword({commit, state}, username) {
    return new Promise((resolve, reject) => {
      let userData = {
        Username: username,
        Pool: state.userPool
      };
      let user = new CognitoUser(userData);
      user.forgotPassword({
        onSuccess: data => {
          console.log(data);
          resolve(data);
        },
        onFailure: err => {
          reject(err);
        }
      });
    });
  },
  confirmPassword({commit, state}, {code, pass, username}) {
    return new Promise((resolve, reject) => {
      //let user = state.userPool.getCurrentUser()
      let userData = {
        Username: username,
        Pool: state.userPool
      };
      console.log(code, pass);
      console.log(userData);
      let user = new CognitoUser(userData);
      user.confirmPassword(code, pass, {
        onSuccess: data => {
          console.log(data);
          resolve(data);
        },
        onFailure: err => {
          reject(err);
        }
      })
    })
  },
  fetchOrders({ state }, payload ) {
    return new Promise((resolve, reject) => {
        console.log('fetching orders')

        let user = state.userPool.getCurrentUser()
        if(!user) { return reject(new Error("user is not logged in"))}

        user.getSession((err, session) => {
          if (err) return reject(err)
          let token = session.getIdToken().getJwtToken();

          let loginKey = `cognito-idp.${config.region}.amazonaws.com/${config.userPoolId}`
          console.log('idp login key', loginKey)

          if(AWS.config.credentials) {
            AWS.config.credentials. clearCachedId();
          }

          AWS.config.region = config.region
          AWS.config.credentials = new AWS.CognitoIdentityCredentials({
            IdentityPoolId: IDENTITY_POOL_ID,
            Logins : {
                    [loginKey] : session.getIdToken().getJwtToken()
                }
          })

          AWS.config.credentials.refresh((error) => {
            if (error) { return reject(error)}


            let key = AWS.config.credentials.identityId
            console.log('identityId', key)

            if(!key) {
              return reject(new Error("Cannot find identityId"))
            }

            const dynamodb = new AWS.DynamoDB({apiVersion: '2012-08-10'})

            let params = {
              TableName: 'spokkz_transactions',
              Key: {
                sub: {
                  S: key
                }
              }
            }

            dynamodb.getItem(params, function(err, data) {
              if(err) { return reject(err)}
              resolve(data)
            })
          })

        })
      })
  },
  createTransaction({commit, state, dispatch},payload) {
    return new Promise((resolve, reject) => {
        console.log('start createTransaction')

        let user = state.userPool.getCurrentUser()
        if(!user) { return reject(new Error("user is not logged in"))}

        user.getSession((err, session) => {
          if (err) return reject(err)
          let token = session.getIdToken().getJwtToken();

          let loginKey = `cognito-idp.${config.region}.amazonaws.com/${config.userPoolId}`
          console.log('idp login key', loginKey)

          if(AWS.config.credentials) {
            AWS.config.credentials.clearCachedId();
          }

          AWS.config.region = config.region
          AWS.config.credentials = new AWS.CognitoIdentityCredentials({
            IdentityPoolId: IDENTITY_POOL_ID,
            Logins : {
                    [loginKey] : session.getIdToken().getJwtToken()
                }
          })

          AWS.config.credentials.refresh((error) => {
            if (error) { return reject(error)}

            let key = AWS.config.credentials.identityId
            console.log('identityId', key)

            if(!key) {
              return reject(new Error("Cannot find identityId"))
            }

            const dynamodb = new AWS.DynamoDB({apiVersion: '2012-08-10'})

            let initializeParams = {
                TableName: 'spokkz_transactions',
                Key: {
                    "sub": {
                      "S": key
                    }
                  },
                ExpressionAttributeValues: {
                  ":tx_default" : {
                    "M": {}
                  },
                  ":email" : {
                    "S": payload.userCreds.email
                  },
                  ":username" : {
                    "S": payload.userCreds.sub
                  },
                },
                ReturnValues: "ALL_NEW",
                ConditionExpression: "attribute_not_exists(orders)",
                UpdateExpression: "SET username = :username, email = :email, orders = :tx_default"
            }

            dynamodb.updateItem(initializeParams, (err, data) => {
              let params = {
                TableName: 'spokkz_transactions',
                Key: {
                  "sub": {
                    "S": key
                  }
                },
                ExpressionAttributeNames: {
                   "#tx_key": payload.tx_key
                },
                ExpressionAttributeValues: {
                  ":tx": {
                    "M": payload.dynamo_db_payload
                  }
                },
                ReturnValues: "ALL_NEW",
                UpdateExpression: "SET orders.#tx_key = :tx"
              }

              dynamodb.updateItem(params, (err, data) => {
                if (err) { return reject(err)}
                resolve(data);
              })
            })
          })

        })
      })
  },

  uploadFile({commit, state, dispatch}, {file, selfie} ) {
    return new Promise((resolve, reject) => {

        let user = state.userPool.getCurrentUser()

        if(!user) { return reject(new Error("user is not logged in"))}

        user.getSession((err, session) => {
          if (err) return reject(err)
          let token = session.getIdToken().getJwtToken();

          let loginKey = `cognito-idp.${config.region}.amazonaws.com/${config.userPoolId}`
          console.log('idp login key', loginKey)

          if(AWS.config.credentials) {
            AWS.config.credentials.clearCachedId();
          }

          AWS.config.region = config.region
          AWS.config.credentials = new AWS.CognitoIdentityCredentials({
            IdentityPoolId: IDENTITY_POOL_ID,
            Logins : {
                    [loginKey] : session.getIdToken().getJwtToken()
                }
          })

          AWS.config.credentials.refresh((error) => {
            if (error) { return reject(error)}

            console.log('credentials')
            console.log(AWS.config.credentials)
            let key = AWS.config.credentials.identityId
            console.log('identityId', key)

            if(!key) {
              return reject(new Error("Cannot find identityId"))
            }



            let fileParams = {
              Bucket: BUCKET_USER_UPLOAD,
              Key: `${key}/document`,
              Body: file,
              ContentType: file.type
            }

            let selfieParams = {
              Bucket: BUCKET_USER_UPLOAD,
              Key: `${key}/selfie`,
              Body: selfie,
              ContentType: selfie.type
            }

            const s3 = new AWS.S3({
              apiVersion: '2006-03-01',
              params: { Bucket: BUCKET_USER_UPLOAD}
            })

            s3.upload(fileParams, function(err, fileResult) {

              if(err) { return reject(err) }

              s3.upload(selfieParams, function(err, selfieResult) {
                if(err) { return reject(err) }
                resolve({file: fileResult, selfie: selfieResult })
              })
            })
          })
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



