<template>
<div class="pageContent">
  <div class="has-shadow box formCard">

    <div class="level is-mobile">
      <a class="level-item active has-text-primary"> Register </a>
      <router-link class="has-text-grey-light level-item" :to="{ name: 'login' }"> Log in</router-link>
    </div>

    <div v-if="error" class="animated fadeIn notification is-warning">
      {{ error }}
    </div>

    <form @submit.prevent="triggerSignUp" class="animated fadeIn">

      <!--First Name-->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="firstName" v-model.trim="firstName" placeholder="First name" required>
        </p>
      </div>

      <!--Last Name-->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="lastName" v-model.trim="lastName" placeholder="Last name" required>
        </p>
      </div>

      <!--Email-->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="email" v-model.trim="email" placeholder="Your Email Address" required>
        </p>
      </div>

      <!-- Password -->
      <div class="field">
        <p class="control">
          <input class="input is-medium" type="password" v-model.trim="pass" pattern=".{8,}" placeholder="Password" required/>
        </p>
        <p class="help"> Requires minimum of 8 characters</p>
      </div>

      <div class="field">
        <div class="control">
          <label class="checkbox">
                  <input type="checkbox" v-model="agreeTerms">
                  <span class="is-size-6a">I agreed to the <a href="../../../static/documents/terms-and-conditions.pdf" target="_blank">Terms and Conditions</a>.</span>
                </label>
        </div>



        <div class="control">
          <label class="checkbox">
                  <input type="checkbox" v-model="agreePrivacyPolicy">
                  <span class="is-size-6a">I have read and agreed to the <a href="../../../static/documents/privacy.pdf" target="_blank"> Privacy Policy</a>.</span>
                </label>
        </div>

        <div class="control">
          <label class="checkbox">
                  <input type="checkbox" v-model="allowedResident">
                  <span class="is-size-6a"> I declare that I'm not a resident of U.S. or China. </span>
                </label>
        </div>
      </div>

      <div class="field">
        <div class="control">
          <button class="auth-button button is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}"> Sign Up</button>
        </div>
      </div>
    </form>

  </div>
</div>
</template>
<script>
import { mapActions } from 'vuex'

export default {
  name: 'Register',
  data() {
    return ({
      error: '',
      firstName: '',
      lastName: '',
      email: '',
      pass: '',
      agreePrivacyPolicy: false,
      agreeTerms: false,
      allowedResident: false,
      isLoading: false
    });
  },
  computed: {
    disableButton() {
      return (!(this.email && (this.pass.length >= 8) && this.agreeTerms && this.agreePrivacyPolicy && this.allowedResident && this.firstName && this.lastName));
    }
  },
  methods: {
    triggerSignUp() {
      this.isLoading = true
      this.error = ""
      this.signUp({
          email: this.email,
          pass: this.pass,
          firstName: this.firstName,
          lastName: this.lastName
        })
        .then(result => {
          this.$store.commit('user/setPreSignUp', { email: this.email, firstName: this.firstName, lastName: this.lastName })
          this.$router.push({ path: '/ico/confirm', query: { email: this.email } })
        })
        .catch(err => {
          console.error('unsuccessfully signed up!', err)
          this.error = err.message
          this.isLoading = false
        })
        .finally(() => {

        })
    },
    ...mapActions('cognito', {
      signUp: 'signUp'
    })
  },
  metaInfo: {
    title: 'Spokkz ICO - Register'
  }
}
</script>

<style lang="scss" scoped>

</style>



