<template>
<div class="pageContent">
  <div class="notification has-text-weight-semibold">
    {{ depositDescription }}
    <router-link to="/ico/orders"> Check your deposits here. </router-link>
  </div>

  <app-personal> </app-personal>
  <app-identity-verification> </app-identity-verification>
  <app-contribution> </app-contribution>
  <app-receiving-wallet> </app-receiving-wallet>
  <app-paying-wallet> </app-paying-wallet>
  <div v-if="completedPayingWallet" class="has-text-centered">
    <router-link v-if="hasBoughtBefore" to="/ico/buy" class="button is-large is-success"> Buy More Tokens </router-link>
    <!-- <router-link v-else to="/ico/buy" class="button is-large is-success"> Buy Tokens </router-link> -->
  </div>

</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import PersonalDetails from './Account/PersonalDetails.vue'
import IdentityVerification from './Account/IdentityVerification.vue'
import AproximateContribution from './Account/ApproximateContribution.vue'
import ReceivingWallet from './Account/ReceivingWallet.vue'
import PayingWallet from './Account/PayingWallet.vue'

export default {
  name: 'Account',
  data() {
    return {
      depositCount: 0
    }
  },
  computed: {
    depositDescription() {
      return `You currently have ${this.depositCount} deposits.`
    },
    ...mapGetters('user', {
      user: 'getUser',
      userAttributes: 'getUserAttributes',
      completedPayingWallet: 'completedPayingWallet',
      hasBoughtBefore: 'hasBoughtBefore'
    })
  },
  methods: {
    ...mapActions('cognito', {
      fetchOrders: 'fetchOrders'
    })
  },
  metaInfo: {
    title: 'Spokkz ICO - Account'
  },
  mounted() {
    this.fetchOrders({})
      .then(data => {
        if (data.Item && data.Item.orders) {
          this.depositCount = Object.keys(data.Item.orders.M).length
        } else {
          console.log('No deposits')
        }
      })
  },
  components: {
    'app-personal': PersonalDetails,
    'app-identity-verification': IdentityVerification,
    'app-contribution': AproximateContribution,
    'app-receiving-wallet': ReceivingWallet,
    'app-paying-wallet': PayingWallet,
  }
}
</script>

<style lang="scss" scoped>

</style>



