<template>
<nav class="navbar has-shadow is-spaced">
  <div class="container">
    <div class="navbar-brand">
      <router-link to="/" class="navbar-item">
        <img src="../../images/ico/logo-dark.png" height="28">
      </router-link>

      <div class="navbar-burger burger" data-target="navbarExampleTransparentExample" @click="toggleMenu()" :class="{'is-active': activeMenu}">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>

    <div id="navbarExampleTransparentExample" class="navbar-menu" :class="{ 'is-active': activeMenu}">
      <div class="navbar-end">
        <div v-if="isAuthenticated" class="navbar-item animated fadeIn">
          <div class="avatarWrapper">
            <div class="avatar">
              <router-link to="/ico/account">
                <span class="has-text-weight-semibold has-text-white is-size-5 is-size-6-touch">
                {{ this.$store.getters['user/getInitials'] }}
              </span>
              </router-link>
            </div>
            <div class="avatarName">



              <span class="has-text-weight-semibold has-text-primary is-size-6-touch">
                <router-link  to="/ico/account">
                {{ this.$store.getters['user/getName'] }}
                </router-link>
              </span>
              <span class="is-size-6a has-text-grey is-size-6-touch">{{ this.$store.state.user.userAttributes.email }} </span>
            </div>
          </div>
        </div>

        <router-link v-show="isAuthenticated" to="/ico/sign_out" class="navbar-item has-text-grey">Sign Out</router-link>
      </div>
    </div>
  </div>
</nav>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Header',
  data() {
    return {
      activeMenu: false
    }
  },
  computed: {
    ...mapGetters('user', {
      isAuthenticated: 'isAuthenticated'
    })
  },
  methods: {
    toggleMenu() {
      this.activeMenu = !this.activeMenu
    }
  }
}
</script>

<style lang="scss" scope>
@import "../../css/ico_base.scss";
.is-spaced {
    padding: 1rem 2rem;
}

.avatarWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: $primary;
    display: flex;
    align-items: center;
    justify-content: center;
}

.avatarName {
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
}

@include touch {
    .is-spaced {
        padding: 0;

    }

    .avatar {
        width: 40px;
        height: 40px;
    }
}
</style>



