<template>
<div class="box pageBox">
  <div class="level is-mobile step">
    <div class="level-left">
      <div class="level-item is-vcentered">
        <span v-if="completedIdentityVerification" class="stepCompleted fadeIn animated">
          <img src="../../../images/stepCompleted.png" alt="">
        </span>
        <span v-else class="stepImage">
          <img src="../../../images/step2.png" alt="">
        </span>
        <p class="stepTitle subtitle has-text-weight-semibold has-text-grey-dark">
          {{this.stepTitle}}
        </p>
      </div>
    </div>
    <div class="level-right">
      <a v-if="completedIdentityVerification" class="button is-outlined is-success completedButton" @click.prevent.stop="toggleCollapse()">
            <span class="buttonName">Completed</span>
          </a>

      <a @click.prevent.stop="toggleCollapse()" class="button is-medium" style="color:#b5b5b5;padding-left:15px;padding-right:15px;border:none;">
          <span v-show="isCollapsed" class="is-medium" style="width:24px">
            <i class="fa fa-angle-right"></i>
          </span>
          <span v-show="!isCollapsed" class="is-medium" style="width:24px">
            <i class="fa fa-angle-down"></i>
          </span>
        </a>

    </div>
  </div>
  <div :class="{ collapsed: isCollapsed }" class="collapsableContent fadeIn animated">
    <p v-if="!completedIdentityVerification" class="notification">
      <span> <i class="fas fa-info-circle"></i> </span> Upload a photo of your
      <span class="has-text-weight-semibold">{{ documentType}}</span> and a
      <span class="has-text-weight-semibold">SELFIE WITH YOUR {{ documentType}}</span> for verification.
    </p>

    <p v-else class="notification">
      Identity document was successfully submitted.
    </p>

    <div v-if="!completedIdentityVerification" class="notification is-warning has-text-weight-semibold">
      <span>
            <i class="fas fa-exclamation-triangle"></i>
          </span> To avoid delay of KYC review process, make sure:
      <div class="content">
        <ul>
          <li>Your document hasn't expired yet</li>
          <li>The details are visible</li>
        </ul>
      </div>
    </div>

    <div v-if="error" class="animated fadeIn notification is-danger">
      {{ error }}
    </div>

    <div v-if="!completedIdentityVerification">

      <div class="field">
        <div class="file has-name is-boxed">
          <label class="file-label" style="width: 100%">
                <input class="file-input" type="file" name="resume" @change="processFile($event)" accept='image/*'>
                <span class="file-cta">
                  <span class="file-icon">
                    <i class="fas fa-upload"></i>
                  </span>
                  <span class="file-label">
                    Upload {{ documentType.toLowerCase()}}
                  </span>
                </span>
                <span v-if="fileName" class="file-name" style="max-width: none;">
                {{ fileName}}
                </span>
              </label>
        </div>
      </div>
      <div class="field">
        <div class="file has-name is-boxed">
          <label class="file-label" style="width: 100%">
                <input class="file-input" type="file" name="resume" @change="processSelfie($event)" accept='image/*'>
                <span class="file-cta">
                  <span class="file-icon">
                    <i class="fas fa-upload"></i>
                  </span>
                  <span class="file-label">
                    Upload selfie holding {{ documentType.toLowerCase()}}
                  </span>
                </span>
                <span v-if="selfieName" class="file-name" style="max-width: none;">
                {{ selfieName}}
                </span>
              </label>
        </div>
      </div>

    </div>
    <div v-else>
    </div>

    <div v-if="!completedIdentityVerification" class="field">
      <div class="control">
        <button style="margin-top: 0.7em" class="auth-button button is-medium is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}" @click="triggerUpload"> Submit </button>
        <p v-if="isLoading" class="help">Please wait. This may take a while.</p>
      </div>
    </div>


  </div>
</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  data() {
    return {
      error: '',
      file: '',
      selfie: '',
      numberOfAttempts: 0,
      fileName: '',
      selfieName: '',
      isCollapsed: true,
      isLoading: false,
    }
  },
  computed: {
    ...mapGetters('user', {
      completedIdentityVerification: 'completedIdentityVerification',
      completedPersonalDetails: 'completedPersonalDetails'
    }),
    documentType() {
      if (!this.$store.state.user.userAttributes["custom:id_type"]) {
        return "PASSPORT or DRIVER'S LICENSE"
      } else {
        return this.$store.state.user.userAttributes["custom:id_type"]
      }
      return
    },
    passport() {
      return this.$store.state.user.userAttributes["custom:passport"]
    },
    stepTitle() {
      let result = "Step 2: Identity Document"

      if (this.completedIdentityVerification) {
        result = "Identity Document"
      }

      return result
    },
    disableButton() {
      if (!this.completedPersonalDetails) {
        return true
      } else if (!this.file || !this.selfie) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
    },
    processFile(event) {
      console.log('processFile', event)
      this.file = event.target.files[0]
      this.fileName = event.target.files[0].name
    },
    processSelfie(event) {
      console.log('processSelfie', event)
      this.selfie = event.target.files[0]
      this.selfieName = event.target.files[0].name
    },
    triggerUpload() {
      this.numberOfAttempts += 1
      this.isLoading = true
      this.error = ''

      let payload = {
        file: this.file,
        selfie: this.selfie
      }

      console.log("triggerUpload payload", payload)

      this.uploadFile(payload)
        .then(result => {

          let payload = [{
            Name: 'custom:passport',
            Value: result.file.Location
          }, {
            Name: 'picture',
            Value: result.selfie.Location
          }]

          this.updateAttribute(payload).then(() => {
            this.loadAttributes().then(() => {
              this.numberOfAttempts = 0
              this.isCollapsed = this.$store.getters['user/completedIdentityVerification']
              this.isLoading = false
            })
          }).
          catch(err => {
            throw err
          })
        })
        .catch(err => {
          if (this.numberOfAttempts > 1) {
            this.error = 'Something went wrong. Please try again. Let us know (team@spokkz.com) if the problem still persists afterwards.'
          } else {
            this.error = 'Something went wrong. Please try again.'
          }
          console.error(err)
          this.isLoading = false
        })
    },
    ...mapActions('cognito', {
      uploadFile: 'uploadFile',
      updateAttribute: 'updateAttribute'
    }),
    ...mapActions('user', {
      loadAttributes: 'loadAttributes'
    })
  }
}
</script>



