<template lang="html">
  <div class="animated fadeIn">
    <h1 class="title has-text-centered is-size-5"> YOUR DEPOSITS </h1>
    <p style="margin-bottom:10px">
      <router-link to="/ico/account" class="button is-medium" style="border-color: transparent;background-color:transparent">
        <span class="icon">
          <i class="fas fa-long-arrow-alt-left"></i>
        </span>
       <span>Back to Account</span>
      </router-link>
  </p>

    <div>
      <div v-for="value in orders" class="box">
          <div class="field is-horizontal">
            <div class="field-label">
              <label class="label">Date:</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <p class="is-size-6"> {{ new Date(Number(value.M.createdAt.N)).toUTCString() }} </p>
                </div>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <label class="label">Declared Amount:</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <p class="is-size-6"> {{ `${value.M.payment.N} ${value.M.selectedCurrency.S}` }} </p>
                </div>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <label class="label">Tx Hash:</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <p class="is-size-6"> {{ value.M.txHash.S }} </p>
                </div>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <label class="label">Status:</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <p class="is-size-6"> {{ value.M.status.S.toUpperCase() }} </p>
                </div>
              </div>
            </div>
          </div>
      </div>
    </div>

    

  </div>
</template>

<script>
import { mapActions } from 'vuex'


export default {
  data() {
    return {
      orders: {}
    }
  },
  methods: {
    ...mapActions('cognito', {
      fetchOrders: 'fetchOrders'
    })
  },
  mounted() {
    this.fetchOrders({})
      .then(data => {

        if (data.Item && data.Item.orders) {
          this.orders = data.Item.orders.M
        }

      })
  }
}
</script>

<style lang="scss">

</style>



