<template lang="html">
  <div class="box pageBox">
    <div class="level is-mobile step">
      <div class="level-left">
        <div class="level-item is-vcentered">
          <span v-if="completedApproximateContribution" class="stepCompleted fadeIn animated">
                <img src="../../../images/stepCompleted.png" alt="">
              </span>

          <span v-else class="stepImage">
                <img src="../../../images/step3.png" alt="">
              </span>
          <p class="stepTitle subtitle has-text-weight-semibold has-text-grey-dark">
            {{this.stepTitle}}
          </p>
        </div>
      </div>
      <div class="level-right">

        <a v-if="completedApproximateContribution" class="button is-outlined is-success completedButton" @click.prevent.stop="toggleCollapse()">
              <span class="buttonName">Completed</span>
            </a>

        <a @click.prevent.stop="toggleCollapse()" class="button is-medium" style="color:#b5b5b5;padding-left:15px;padding-right:15px;border:none;">
              <span v-show="isCollapsed" class="is-medium" style="width:24px">
                <i class="fa fa-angle-right"></i>
              </span>
              <span v-show="!isCollapsed" class="is-medium" style="width:24px">
                <i class="fa fa-angle-down"></i>
              </span>
            </a>
      </div>
    </div>

    <div :class="{ collapsed: isCollapsed }" class="collapsableContent fadeIn animated">
      <p class="notification">
        <span>
              <i class="fas fa-info-circle"></i>
            </span>Please specify your expected contribution in <strong>USD</strong> in order to allows us to estimate the demand.
      </p>
      <form @submit.prevent="triggerContribution">
        <label class="label">Expected Contribution in $USD</label>
        <div class="field has-addons">
          <p class="control"> <a class="button is-static"> $ </a> </p>
          <p class="control">
            <input class="input" type="number" placeholder="Amount" v-model="exp_contribution" required>
          </p>
        </div>
        <div class="field">
          <div class="control">
            <button class="auth-button button is-medium is-primary" :disabled="disableButton" :class="{'is-loading': isLoading}"> Submit Estimation </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  data() {
    return {
      exp_contribution: '',
      isLoading: false,
      isCollapsed: true
    }
  },
  computed: {
    stepTitle() {
      let result = "Step 3: Expected Contribution"

      if (this.completedApproximateContribution) {
        result = "Expected Contribution"
      }

      return result
    },
    disableButton() {
      if (!this.completedPersonalDetails) {
        return true
      } else if (!this.exp_contribution) {
        return true
      } else if (this.exp_contribution == this.$store.state.user.userAttributes["custom:exp_contribution"]) {
        return true
      } else {
        return false
      }
    },
    ...mapGetters('user', {
      completedApproximateContribution: 'completedApproximateContribution',
      completedPersonalDetails: 'completedPersonalDetails'
    })
  },
  methods: {
    triggerContribution() {
      this.isLoading = true
      this.updateAttribute([{
          Name: "custom:exp_contribution",
          Value: this.exp_contribution
        }]).then(result => {
          console.log('est contribution success');
          this.loadAttributes().then(() => {
            console.log('sync fields in loadAttributes')
            this.isCollapsed = true;
            this.syncFields();
          })
        })
        .catch(err => {
          console.error('unsuccessfully est contribution', err)
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
    },
    ...mapActions('cognito', {
      updateAttribute: 'updateAttribute'
    }),
    ...mapActions('user', {
      loadAttributes: 'loadAttributes'
    }),
    syncFields() {
      this.exp_contribution = this.$store.state.user.userAttributes["custom:exp_contribution"]
    },
  },
  created() {
    // console.log('sync fields in created')
    this.syncFields()
  },
}
</script>

<style lang="css">
</style>



