<template lang="html">
  <div class="box pageBox">
    <div class="level is-mobile step">
      <div class="level-left">
        <div class="level-item is-vcentered">
          <p class="stepTitle subtitle has-text-weight-semibold has-text-grey-dark">
            {{this.stepTitle}}
          </p>
        </div>
      </div>
    </div>
    <div :class="{ collapsed: false }" class="collapsableContent fadeIn animated">
      <p class="has-text-weight-semibold is-size-4" style="padding: 10px"> 1. Set Payment Amount </p>
      <div class="notification">
        <span> <i class="fas fa-info-circle"></i></span> SPOKKZ Token Price: $0.08
        <br>
        <br>
        <p v-for="option in currencyOptions">
          1 {{ option.text }} = {{ cryptoUSDRates[option.text]}} USD
        </p>
      </div>
      <div class="setPaymentWrapper box">
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label">You will pay</label>
          </div>
          <div class="field-body">
            <div class="field is-expanded">
            <div class="field has-addons">
              <p class="control">
                <span class="select is-medium">
                  <select v-model="selectedCurrency" @change="updateConversions">
                    <option v-for="option in currencyOptions" :value="option.value">
                      {{ option.text }}
                    </option>
                  </select>
                </span>
              </p>
              <p class="control is-expanded">
                <input class="input is-medium" type="number"  min="0" placeholder="0" v-model.number="computedPayment">
              </p>
            </div>
             <p class="help has-text-weight-semibold"> ${{ this.usdEquivalent }} USD (estimated)</p>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label">You will get*</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="field has-addons">
                <p class="control">
                  <a class="button is-static is-medium spokkzButton has-text-weight-bold" style="width: 102px">
                    SPKZ
                  </a>
                </p>
                <p class="control is-expanded">
                  <input class="input is-medium" type="number" min="0" placeholder="0" v-model.number="computedAmount">
                </p>
              </div>

            </div>
          </div>
         </div>
         <p class="smallPrint">* This is an estimated exchange rate. Final exchange rate is locked when the payment is confirmed on the blockchain.</p>
         <div v-if="computeBonus" style="margin-top: 10px;">
         <a class="button is-large is-success is-fullwidth fadeIn animated">
           <span class="icon is-large">
             <i class="fas fa-gift"></i>
           </span>
           <span>{{computeBonus}}</span>
         </a>
         <p class="help has-text-centered"> *For 27 Aug - 09 Sept only </p>
         </div>
     </div>
    </div>
    <div :class="{ collapsed: false }" class="collapsableContent fadeIn animated">
      <p class="has-text-weight-semibold is-size-4" style="padding: 10px"> 2. Send Payment </p>
      <div v-if="selectedCurrency == 'ETH'"class="notification is-warning has-text-weight-semibold">
        <span>
              <i class="fas fa-exclamation-triangle"></i>
            </span> Recommended Gas Limit and Gas Price
        <br>
        <br>
        <p> Minimum gas limit: 110 000</p>
        <p> Minimum gas price: 15 GWEI</p>
      </div>
      <div class="setPaymentWrapper box">
        <div class="field">
          <label class="label">Send {{ payment }} {{ selectedCurrency }} to</label>
          <div class="control">
            <input class="input is-medium" type="text" :value="computedDepositAddress" :disabled="true"> </input>
          </div>
        </div>
        <div class="field">
          <div class="control has-text-centered">
            <button class="button is-medium" @click="triggerCopyToClipboard()">Copy Address</button>
          </div>
       </div>
        <div class="field">
          <div v-if="selectedCurrency == 'ETH'" class="has-text-centered">
            <img src="../../../images/qr/ETH.png" alt="">
          </div>
          <div v-if="selectedCurrency == 'BTC'" class="has-text-centered">
            <img src="../../../images/qr/BTC.png" alt="">
          </div>
          <!-- <div v-if="selectedCurrency == 'ONT'" class="has-text-centered">
            <img src="../../../images/qr/ONT.png" alt="">
          </div> -->
        </div>
     </div>
   </div>

   <div :class="{ collapsed: false }" class="collapsableContent fadeIn animated">
      <p class="has-text-weight-semibold is-size-4" style="padding: 10px"> 3. Proof of Payment (Optional) </p>
      <div v-if="!submitted" class="setPaymentWrapper box">
        <div class="notification">
          The SPOKZZ tokens will be deposited into your wallet seven (7) days after ICO ends. Your orders will appear in your account in the next few hours.
        </div>

        <div class="field">
          <label class="label">Tx Hash</label>

          <div class="control">
            <input class="input" type="text" v-model="txHash"> </input>
          </div>
          <p class="help"> This is the transaction ID / TX-ID / Hash which can be found in your private sending wallet next to the payment.</p>
        </div>
      </div>

      <div v-else class="setPaymentWrapper box">
        <p> <span class="has-text-weight-bold">Order No: </span> {{ this.$store.state.user.userAttributes.sub}}</p>
        <p> <span class="has-text-weight-bold">Status: </span> Pending </p>
      </div>
  </div>

  <div :class="{ collapsed: false }" class="collapsableContent fadeIn animated">
    <div class="field is-grouped">
      <p class="control">
        <a class="button" @click.prevent.stop="()=> { showCancelDepositModal = true}"> Cancel </a>
      </p>
      <p class="control">
        <button @click="triggerConfirm" class="button is-primary" :class="{'is-loading': isLoading}" > Payment Completed </button>
      </p>
    </div>
  </div>

  <div class="modal" :class="{'is-active': showCancelDepositModal}">
    <div class="modal-background"></div>
    <div class="modal-card">
      <section class="modal-card-body box">
      <p class="subtitle has-text-centered"> Do you really want to cancel the deposit? </p>
      <p class="has-text-centered">
        <router-link to="/ico/account" class="button is-primary">
          Yes, cancel it
        </router-link>
        <a class="button" @click.prevent.stop="() => { showCancelDepositModal = false} ">Do not cancel</a>
      </p>
      </section>
    </div>
  </div>


</div>
</template>

<script>
import copy from 'copy-to-clipboard'
import EthereumQRPlugin from 'ethereum-qr-code'
import axios from 'axios'
import { mapGetters, mapActions } from 'vuex'

const qr = new EthereumQRPlugin()

export default {
  data() {
    return {
      showCancelDepositModal: false,
      submitted: false,
      isLoading: false,
      txHash: '',
      errors: '',
      isCollapsed: false,
      selectedCurrency: '',
      payment: 0,
      amount: 0,
      usdEquivalent: 0,
      currencyOptions: [],
      cryptoUSDRates: {
        "ETH": 0,
        "BTC": 0,
        "ONT": 0
      },
      depositAddresses: {
        "ETH": "0x125CB18f52C57491d1f9C6c1d19b324e9F27940c",
        "BTC": "1BUdJy23QkoqEskM9L15NBd8q2QLyeBHGb",
        // "ZIL": "0xe2C93D1E644D7cbF4A42eEd11860f9FA535388DC",
        "ONT": "AHcjorr2q3c5eTSeS3c6S7q2oDyfhAKJDt"
      }
    }
  },
  computed: {
    disableButton() {
      return !(this.txHash)
    },
    computeBonus() {
      if (this.usdEquivalent > 100000) {
        return "*You get 20% Pre-sale bonus!"
      } else if (this.usdEquivalent <= 100000 && this.usdEquivalent >= 20000) {
        return "*You get 15% Pre-sale bonus!"
      } else if (this.usdEquivalent > 0 && this.usdEquivalent < 20000) {
        return "*You get 10% Pre-sale bonus!"
      } else {
        return ""
      }
    },
    computedDepositAddress() {
      return this.depositAddresses[this.selectedCurrency]
    },
    computedPayment: {
      get() {
        // this.selectedCurrency
        return this.payment
      },
      set(newValue) {
        // this.selectedCurrency
        this.payment = newValue
        this.usdEquivalent = this.payment * this.cryptoUSDRates[this.selectedCurrency]
        this.amount = this.usdEquivalent / 0.08
      }
    },
    computedAmount: {
      get() {
        // this.selectedCurrency
        return this.amount
      },
      set(newValue) {
        // this.selectedCurrency
        this.amount = newValue
        this.usdEquivalent = this.amount * 0.08
        this.payment = this.usdEquivalent / this.cryptoUSDRates[this.selectedCurrency]
      }
    },
    stepTitle() {
      let result = "Buy Tokens"

      // if (this.completedPersonalDetails) {
      //   result = "Personal Details"
      // }

      return result
    }
  },
  methods: {
    triggerConfirm() {
      this.isLoading = true
      let createdAt = Date.now()
      let tx_key = `${this.$store.state.user.userAttributes.sub}-${createdAt}`

      console.log("key", tx_key)

      let userCreds = {
        email: this.$store.state.user.userAttributes.email,
        sub: this.$store.state.user.userAttributes.sub
      }

      let payload = {
        tx_key: tx_key,
        dynamo_db_payload: {},
        userCreds: userCreds
      }

      payload.dynamo_db_payload = {
        "selectedCurrency": { "S": this.selectedCurrency },
        "payment": { "N": this.computedPayment.toString() },
        "amount": { "N": this.computedAmount.toString() },
        "usdEquivalent": { "N": this.usdEquivalent.toString() },
        "cryptoUSDRates": { "N": this.cryptoUSDRates[this.selectedCurrency].toString() },
        "status": { "S": "pending" },
        "createdAt": { "N": createdAt.toString() },
      }

      if (this.txHash) {
        payload.dynamo_db_payload["txHash"] = { "S": this.txHash }
      } else {
        payload.dynamo_db_payload["txHash"] = { "S": "Not Specified" }
      }

      console.log(payload)

      this.createTransaction(payload)
        .then(result => {
          setTimeout(() => {
            this.$router.push({ path: '/ico/orders' })
            this.isLoading = false
          }, 2000)
        }).catch(err => {
          this.isLoading = false
        })
    },
    triggerCopyToClipboard() {
      copy(this.computedDepositAddress);
    },
    updateConversions() {
      this.usdEquivalent = this.payment * this.cryptoUSDRates[this.selectedCurrency]
      this.amount = (this.payment * this.usdEquivalent) / 0.08
      // this.payment = this.usdEquivalent / this.cryptoUSDRates[this.selectedCurrency]
    },
    getRates(sym) {
      axios.get('https://min-api.cryptocompare.com/data/price', {
          params: {
            fsym: sym,
            tsyms: "USD"
          }
        })
        .then(response => {
          console.log(this.cryptoUSDRates)
          this.cryptoUSDRates[sym] = response.data["USD"]

        })
        .catch(error => {
          console.log('error getting rates', error)
          this.errors = `Cannot get exchange rate for ${sym}`
        })
    },
    ...mapActions('cognito', {
      updateAttribute: 'updateAttribute',
      createTransaction: 'createTransaction'
    }),
    ...mapActions('user', {
      loadAttributes: 'loadAttributes'
    })
  },
  created() {
    if (this.$store.state.user.userAttributes["custom:eth_address"]) {
      this.currencyOptions.push({
        text: 'ETH',
        value: 'ETH'
      })

      this.selectedCurrency = 'ETH'

      this.getRates('ETH')
    }

    if (this.$store.state.user.userAttributes["custom:btc_address"]) {
      this.currencyOptions.push({
        text: 'BTC',
        value: 'BTC'
      })

      if (!this.selectedCurrency) {
        this.selectedCurrency = 'BTC'
      }
      this.getRates('BTC')
    }
    if (this.$store.state.user.userAttributes["custom:zil_address"]) {
      if (!this.selectedCurrency) {
        this.selectedCurrency = 'ONT'
      }

      this.currencyOptions.push({
        text: 'ONT',
        value: 'ONT'
      })

      this.getRates('ONT')
    }

  }
}
</script>

<style lang="scss" scope>
.modal-content {
    background-color: white;
}
</style>



