</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Datepicker from 'vuejs-datepicker'
import moment from 'moment'

export default {
  data() {
    return {
      given_name: '',
      family_name: '',
      id_type: '',
      id_number: '',
      issue_date: '',
      expiry_date: '',
      nationality: '',
      country: '',
      telegram: '',
      isCollapsed: false,
      isLoading: false,

    }
  },
  computed: {
    stepTitle() {
      let result = "Step 1: Personal Details"

      if (this.completedPersonalDetails) {
        result = "Personal Details"
      }

      return result
    },
    disableButton() {
      let result = true

      if (this.id_type == "DRIVING LICENSE") {
        result = !(
          this.given_name &&
          this.family_name &&
          this.id_type &&
          this.id_number &&
          this.issue_date &&
          this.nationality &&
          this.country)

      } else {
        result = !(
          this.given_name &&
          this.family_name &&
          this.id_type &&
          this.id_number &&
          this.expiry_date &&
          this.issue_date &&
          this.nationality &&
          this.country)
      }
      return result
    },
    ...mapGetters('user', {
      completedPersonalDetails: 'completedPersonalDetails'
    })
  },
  methods: {
    customFormatter(date) {
      return moment(date).format("D/M/YYYY")
    },
    triggerPersonal() {
      this.isLoading = true
      console.log(this.issue_date);
      console.log(this.expiry_date);
      this.updateAttributes({
          email: this.email,
          given_name: this.given_name,
          family_name: this.family_name,
          id_type: this.id_type,
          id_number: this.id_number,
          issue_date: this.issue_date,
          expiry_date: this.expiry_date,
          nationality: this.nationality,
          country: this.country,
          telegram: this.telegram,

        })
        .then(result => {
          console.log('personal information success');
          this.loadAttributes().then(() => {
            console.log('sync fields in loadAttributes')
            this.syncFields();
            this.isLoading = false
          })
        })
        .catch(err => {
          console.error('unsuccessfully updated attributes up!', err)
          this.isLoading = false
        })
        .finally(() => {

        })
    },
    syncFields() {
      this.email = this.$store.state.user.userAttributes.email
      this.given_name = this.$store.state.user.userAttributes.given_name
      this.family_name = this.$store.state.user.userAttributes.family_name
      this.id_type = this.$store.state.user.userAttributes["custom:id_type"]
      this.id_number = this.$store.state.user.userAttributes["custom:id_number"]
      this.issue_date = this.$store.state.user.userAttributes["custom:issue_date"]
      this.expiry_date = this.$store.state.user.userAttributes["custom:expiry_date"]
      this.nationality = this.$store.state.user.userAttributes["custom:nationality"]
      this.country = this.$store.state.user.userAttributes["custom:country"]
      this.telegram = this.$store.state.user.userAttributes["custom:telegram"]
      this.isCollapsed = this.$store.getters['user/completedPersonalDetails']
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
    },
    ...mapActions('cognito', {
      updateAttributes: 'updateAttributes'
    }),
    ...mapActions('user', {
      loadAttributes: 'loadAttributes'
    })
  },
  created() {
    // console.log('sync fields in created')
    this.syncFields()
  },
  components: {
    Datepicker
  }
}
</script>

<style lang="scss" scope>
</style>



